export class todoListController {
    constructor($scope, $mdDialog, todoService, authorizationService, navigationService) {
        this.$scope = $scope,
        this.$mdDialog = $mdDialog;
        this.todoService = todoService;
        this.authorizationService = authorizationService;
        this.navigationService = navigationService;
        this.initialize();
    }

    initialize() {
        this.navigationService.isBusy = true;
        this.authorizationService.checkLoginOnInit()
            .then((res, rej) => {
                this.getTodos();
            });
    }

    isBusy() {
        return this.navigationService.isBusy;
    }

    isLoggedIn() {
        return this.authorizationService.isUserLoggedIn();
    };

    getTodos() {
        this.navigationService.isBusy = true;
        this.list = [];
        this.todoService.getTodos(
            this.authorizationService.getAccessToken(),
            this.authorizationService.getUserData().id
        ).then((response) => {
            return response.json()
                .then((json) => {
                    this.list = json;
                    this.navigationService.isBusy = false;
                    this.$scope.$apply();
                });
        }).catch((error) => {
            console.log(error);
        });
    }

    createTodo(event) {
        this.$mdDialog.show({
            controller: 'editTodoController',
            controllerAs: 'editTodoVM',
            templateUrl: './views/edit-todo.html',
            parent: angular.element(document.body),
            targetEvent: event,
            clickOutsideToClose: true,
            locals: {
                todo: {}
            }
        }).then((response) => {
            console.log(response);
        }).catch((error) => {
            console.log(error);
        }).finally(() => {
            this.getTodos();
        });
    };
}


