export class editTodoController {
    constructor(todoService, navigationService, authorizationService, $mdDialog, todo) {
        this.$mdDialog = $mdDialog;
        this.navigationService = navigationService;
        this.authorizationService = authorizationService;
        this.todoService = todoService;
        this.todo = todo;
    }

    cancel() {
        this.$mdDialog.cancel();
    }

    editTodo(todo) {
        let actionType = 'createTodo';
        if (todo && todo._id) {
            actionType = 'updateTodo';
        }
        
        this.navigationService.isBusy = true;

        this.todoService[actionType](
            this.authorizationService.getAccessToken(),
            this.authorizationService.getUserData().id,
            todo
        ).then((response) => {
            this.$mdDialog.hide(response);
        }).catch((error) => {
            console.log(error);
        }).finally(() => {
            this.navigationService.isBusy = false;
            this.navigationService.showMessage('Zapisano zadanie');
        });
    }
}


