let accessToken;
let isLoggedIn = false;
let userData = {};

export default class authorizationService {
    constructor($facebook) {
        this.$facebook = $facebook;
    }

    isUserProfileFetched() {
        return new Promise();
    }

    checkLoginOnInit() {
        return new Promise((resolve) => {
            this.$facebook.getLoginStatus()
                .then((loginStatus) => {
                    if (loginStatus.status === 'connected') {
                        this.$facebook.api("/me")
                            .then((userDataResponse) => {
                                isLoggedIn = true;
                                userData = {
                                    name: userDataResponse.name,
                                    id: userDataResponse.id
                                };
                                accessToken = loginStatus.authResponse.accessToken;
                                resolve();
                            });
                    }
                });
        });
    }

    login(successCallback, errorCallback) {
        this.$facebook.login()
            .then(() => {
                this.$facebook.api("/me")
                    .then((userDataResponse) => {
                        isLoggedIn = true;

                        this.$facebook.getLoginStatus().then((loginStatus) => {
                            userData = {
                                name: userDataResponse.name,
                                id: userDataResponse.id
                            };
                            accessToken = loginStatus.authResponse.accessToken;
                        });
                    })
                    .catch((error) => {
                        isLoggedIn = false;
                        errorCallback(error);
                    });
            });
    }

    logout() {
        this.$facebook.logout()
            .then(() => {
                accessToken = undefined;
                isLoggedIn = false;
                userData = {};
            });
    }

    isUserLoggedIn() {
        return isLoggedIn;
    }

    getUserData() {
        return userData;
    }

    getAccessToken() {
        return accessToken;
    }
}

authorizationService.$inject = ['$facebook'];


