import { facebookConfig, facebookInitialization } from './app.config';
import { routes } from './app.routes';

import { todoComponent } from './components/todo/todo.component';

import { deleteTodoController } from './controllers/delete-todo.controller';
import { editTodoController } from './controllers/edit-todo.controller';
import { navbarController } from './controllers/navbar.controller';
import { todoController } from './controllers/todo.controller';
import { todoListController } from './controllers/todo-list.controller';

import authorizationService from './services/authorization.service';
import navigationService from './services/navigation.service';
import todoService from './services/todo.service';

angular.module('mrplanner', ['ui.router', 'ngMaterial', 'ngFacebook'])
    .config(facebookConfig)
    .config(routes)

    .component('mrpTodo', todoComponent)

    .controller('deleteTodoController', deleteTodoController)
    .controller('editTodoController', editTodoController)
    .controller('navbarController', navbarController)
    .controller('todoController', todoController)
    .controller('todoListController', todoListController)

    .service('authorizationService', authorizationService)
    .service('navigationService', navigationService)
    .service('todoService', todoService)

    .run(facebookInitialization);


