export class todoController {
    constructor($mdDialog, todoService, authorizationService, navigationService) {
        this.$mdDialog = $mdDialog;
        this.todoService = todoService;
        this.authorizationService = authorizationService;
        this.navigationService = navigationService;
    }

    getDateFrom(date) {
        return new Date(date);
    }

    deleteTodo(event, todoId) {
        console.log(event);
        console.log(todoId);
        this.$mdDialog.show({
            controller: 'deleteTodoController',
            controllerAs: 'deleteTodoVM',
            templateUrl: './views/delete-todo.html',
            parent: angular.element(document.body),
            targetEvent: event,
            clickOutsideToClose: true,
            locals: {
                todoId: todoId
            }
        }).then((response) => {
            console.log(response);
        }, (error) => {
            console.log(error);
        });
    }

    editTodo(event, todo) {
        this.$mdDialog.show({
            controller: 'editTodoController',
            controllerAs: 'editTodoVM',
            templateUrl: './views/edit-todo.html',
            parent: angular.element(document.body),
            targetEvent: event,
            clickOutsideToClose: true,
            locals: {
                todo: angular.copy(todo)
            }
        }).then((response) => {
            console.log(response);
        }, (error) => {
            console.log(error);
        });
    };
}


