export class navbarController {
    constructor(authorizationService) {
        this.authorizationService = authorizationService;
        this.authorizationService.checkLoginOnInit();
    };

    getUserName() {
        return this.authorizationService.getUserData().name;
    };

    getUserId() {
        return this.authorizationService.getUserData().id;
    };

    isLoggedIn() {
        return this.authorizationService.isUserLoggedIn();
    };

    login() {
        this.authorizationService.login(function (userData) {
            this.userId = userData.id;
        });
    };

    logout() {
        this.authorizationService.logout();
    };
}


