export default class todoService {
    constructor ($http) {
        this.$http = $http;
    }

    getTodos(accessToken, userId) {
        let requestConfig = {
            method: 'GET',
            headers: {
                'Authorization': 'Bearer ' + accessToken,
                'Accept': 'application/json;'
            },
            cache: 'default'
        };

        return fetch('/api/todos/' + userId, requestConfig);
    }

    createTodo(accessToken, userId, todo) {
        let requestConfig = {
            url: '/api/todo/' + userId,
            method: 'POST',
            headers: {
                'Authorization': 'Bearer ' + accessToken,
                'Accept': 'application/json;',
                'Content-Type': 'application/json'
            },
            cache: 'default',
            data: { todo : todo }
        };

        return this.$http(requestConfig);
    }

    updateTodo(accessToken, userId, todo) {
        let requestConfig = {
            url: '/api/todo/' + userId + '/',
            method: 'PUT',
            headers: {
                'Authorization': 'Bearer ' + accessToken,
                'Accept': 'application/json;',
                'Content-Type': 'application/json'
            },
            cache: 'default',
            data: { todo : todo }
        };

        return this.$http(requestConfig);
    }

    deleteTodo(accessToken, userId, todoId) {
        let requestConfig = {
            url: '/api/todo/' + userId + '/' + todoId,
            method: 'delete',
            headers: {
                'Authorization': 'Bearer ' + accessToken,
                'Accept': 'application/json;'
            },
            cache: 'default'
        };

        return this.$http(requestConfig);
    }
}


