export default class navigationService {
    constructor($mdToast) {
        this.$mdToast = $mdToast;
        this.isBusy = true;
    }

    showMessage(message) {
        this.$mdToast.show(
                this.$mdToast.simple()
                .textContent(message)
                .hideDelay(3000)
        )
    }
}


